@use './../utilities/flags/flag-icons.scss';
// Flags - https://flagicons.lipis.dev/

$header-height: 62px;
$menu-mq-max: 1199.98px;
$menu-mq-min: 1200px;

.header {
	padding-left: 16px;
	padding-right: 16px;
	min-height: $header-height;
	position: fixed;
	z-index: 100;
	width: 100%;
	background: #fff;
	box-shadow: -1.5px 4.8px 5px 0 rgba(0, 0, 0, 0.05);
	top: 0;
	left: 0;
	@include transition(all 0.5s);
	@include mq($max-width: 576px) {
		padding-left: 0;
		padding-right: 0;
	}
	.row {
		max-width: 1920px;
		margin: 0 auto;
		& > div:not(.menu-col) {
			padding-top: 4px;
			padding-bottom: 4px;
		}
	}

	// CTA buttons
	.button--header {
		min-width: unset;
		margin: 0 5px;
		padding: 12px 16px;
		font-size: 16px;
	}
}

.logo {
	display: inline-block;
	img {
		display: block;
		max-width: 180px;
		width: 100%;
	}
}

.dropdown-menu {
	position: relative;
	@include mq($max-width: 567px) {
		display: none;
	}
	&__dropdown {
		display: none;
		top: calc(100% + 6px);
		position: absolute;
		right: 0;
		border: 1px solid rgba(0, 0, 0, 0.1);
		background: #fff;
		z-index: 100;
		&.active {
			display: block;
			padding: 12px;
		}
		li:not(:last-child) {
			margin-bottom: 12px;
		}
		a {
			width: 100%;
			min-width: unset;
			padding: 12px 15px;
			font-size: 16px;
			white-space: nowrap;
		}
	}
}

.rtl {
	.header .navigation-dropdown__trigger {
		right: unset;
		left: 0.5em;
	}
	i.flag:not(.icon) {
		margin: 0 0 0.5em 0;
	}
}

.menu {
	@media (max-width: $menu-mq-max) {
		position: fixed;
		display: flex;
		flex-direction: column;
		width: 100%;
		max-width: 340px;
		background-color: #ffffff;
		right: 0;
		top: 0;
		height: 100%;
		overflow: hidden;
		transform: translateX(100%);
		z-index: 1099;

		&.active {
			transform: translateX(0%);
		}
		&.animated {
			transition: all 0.5s ease;
		}
	}
	&__list {
		display: flex;
		align-items: center;
		justify-content: space-around;
		flex-grow: 1;
		@media (max-width: $menu-mq-max) {
			flex-direction: column;
			justify-content: start;
			align-items: stretch;
			overflow-x: hidden;
			overflow-y: auto;
		}
	}
	&__item {
		display: inline-block;
		// Keep hover color when cursor goes to dropdown menu
		&:hover .menu__link-title {
			color: $link-color;
		}
		&--mobile {
			display: none;
			@media (max-width: $menu-mq-max) {
				display: block;
			}
		}
		&--button {
			padding: 12px 16px;
			a {
				width: 100%;
			}
		}
		&--button + &--button {
			padding-top: 0;
		}

		@media (min-width: $menu-mq-min) {
			&:hover .sub-menu {
				margin-top: 0px;
				visibility: visible;
				opacity: 1;
			}
		}
		@media (max-width: $menu-mq-max) {
			line-height: 1;
			margin: 0;
			display: block;
			a i:not(.flag) {
				position: absolute;
				height: 50px;
				width: 50px;
				top: 0;
				right: 0;
				text-align: center;
				line-height: 50px;
				transform: rotate(-90deg);
			}
		}
	}
	&__link-title {
		font-size: 16px;
		font-weight: 400;
		color: #000000;
		position: relative;
		transition: color 0.3s ease;
		text-decoration: none;
		text-transform: uppercase;
		cursor: pointer;
		&:hover {
			color: $link-color;
		}
		i:not(.flag) {
			font-size: 14px;
			padding-left: 2px;
			@media (max-width: $menu-mq-max) {
				transform: rotate(-90deg);
				margin-left: auto;
			}
		}
		@media (min-width: $menu-mq-min) {
			line-height: $header-height;
		}
		@media (max-width: $menu-mq-max) {
			min-height: 52px;
			padding: 12px 16px;
			display: flex;
			align-items: center;
			justify-content: start;
			border-bottom: 1px solid rgba(0, 0, 0, 0.1);
		}
	}

	.sub-menu {
		margin-top: 20px;
		padding: 16px 28px;
		min-width: 280px;
		max-width: 350px;
		position: absolute;
		top: 100%;
		background-color: #ffffff;
		z-index: 100;
		opacity: 0;
		visibility: hidden;
		border: 1px solid rgba(0, 0, 0, 0.1);
		max-height: calc(100dvh - $header-height);
		overflow-y: auto;
		transition: opacity, margin 0.3s ease;

		ul li {
			line-height: 1;
		}
		a {
			padding: 6px 0;
			display: block;
			font-size: 16px;
			color: #151515;
			line-height: 20px;
			transition: color 0.3s ease;
			text-decoration: none;
			text-transform: capitalize;
			&:hover {
				color: $link-color;
			}
			@media (max-width: $menu-mq-max) {
				padding: 12px 0;
			}
		}

		&--language {
			right: 0;
			width: 90vw;
			max-width: 720px;
		}

		@media (max-width: $menu-mq-max) {
			margin-top: 0px;
			padding: 16px;
			width: 100%;
			height: 100%;
			position: absolute;
			top: $header-height;
			left: 0;
			transform: translateX(100%);
			max-width: none;
			min-width: auto;
			border: 0;
			overflow-y: auto;
			visibility: visible;
			opacity: 1;
			transition: transform 0.5s ease;
			&.active {
				display: flex !important;
				flex-direction: column;
				flex-wrap: nowrap;
				transform: translateX(0%);
			}
		}
	}

	.mega-menu {
		width: 100%;
		display: flex;
		gap: 36px 60px;

		@media (min-width: $menu-mq-min) {
			position: fixed;
			top: 62px;
			padding: 24px 32px;
			left: 50%;
			width: calc(100vw - 100px);
			max-width: 1100px;
			transform: translateX(-50%);
			flex-wrap: wrap;
		}

		&--column-2 {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
		}
		&--column-3 {
			display: grid;
			grid-template-columns: repeat(3, 1fr);
		}
		&--column-4 {
			display: grid;
			grid-template-columns: repeat(4, 1fr);
		}
		&__item {
		}
		&__item-title {
			margin-bottom: 8px;
			padding: 8px 8px 12px;
			font-size: 16px;
			color: $color-primary;
			font-weight: 500;
			color: #151515;
			display: block;
			border-bottom: 1px solid #696969;
		}
		ul {
			padding-left: 32px;
		}
	}

	.language {
		@media (min-width: $menu-mq-min) {
			position: relative;
		}
		&__menu {
			display: flex;
			width: 100%;
			@media (min-width: $menu-mq-min) {
				width: auto;
				display: inline-block;
				line-height: 1;
				padding: 8px;
				border: 1px solid #f5f5f5;
				background-color: #f5f5f5;
				.sub-menu {
					right: 0;
				}
			}
		}
		&__menu-name {
			display: none;
			@media (max-width: $menu-mq-max) {
				display: inline-block;
			}
		}
		&__menu-icon {
			margin-right: 2px;
			@media (max-width: $menu-mq-max) {
				margin-right: 8px;
			}
		}
		ul {
			display: grid;
			grid-template-columns: 1fr;
			gap: 8px 24px;
			@media (min-width: $menu-mq-min) {
				grid-template-columns: repeat(4, minmax(100px, 1fr));
			}
		}
		.language-item {
			display: flex;
			align-items: center;
			padding: 8px;
			gap: 8px;
			&.active {
				background-color: rgba(0, 73, 158, 0.05);
				&:after {
					content: '\f00c';
					font-family: 'Font Awesome 5 Free';
					font-weight: 700;
					font-size: 14px;
					color: rgba(116, 116, 116);
					display: inline-block;
					text-align: center;
					margin-left: auto;
				}
			}
			.flag {
				flex: 0 0 auto;
				width: 32px;
				height: 32px;
				border-radius: 50%;
			}
		}
	}

	// Mobile head
	&__mobile-head {
		display: none;
		height: $header-height;
		align-items: stretch;
		border-bottom: 1px solid rgba(0, 0, 0, 0.1);
		justify-content: space-between;
		position: sticky;
		z-index: 105;
		background-color: #ffffff;
		top: 0;

		@media (max-width: $menu-mq-max) {
			display: flex;
			&.active .menu__back {
				display: flex;
			}
		}
	}
	&__back,
	&__close {
		width: 56px;
		cursor: pointer;
		color: #151515;
		font-size: 16px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex: 0 0 auto;
	}
	&__back {
		border-right: 1px solid rgba(0, 0, 0, 0.1);
		display: none;
	}
	&__close {
		border-left: 1px solid rgba(0, 0, 0, 0.1);
	}
	&__current-title {
		padding: 8px;
		font-size: 16px;
		font-weight: 500;
		color: #151515;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

.mobile-menu-trigger {
	display: none;
	height: 30px;
	width: 30px;
	margin-left: 16px;
	cursor: pointer;
	span {
		display: block;
		height: 2px;
		background-color: #333333;
		width: 24px;
		position: relative;
		&:before,
		&:after {
			content: '';
			position: absolute;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: #333333;
		}
		&:before {
			top: -6px;
		}
		&:after {
			top: 6px;
		}
	}
	@media (max-width: $menu-mq-max) {
		display: flex;
		align-items: center;
		justify-content: center;
	}
}
.menu-overlay {
	position: fixed;
	background-color: rgba(0, 0, 0, 0.5);
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 1098;
	visibility: hidden;
	opacity: 0;
	transition: all 0.5s ease;
	&.active {
		visibility: visible;
		opacity: 1;
	}
}

// Deprecated styles

.header.fixed {
	ul .navigation__lang .navigation-dropdown {
		left: 0;
	}
}

.navigation {
	margin-left: auto;
	margin-right: auto;
	width: 100%;
	max-width: 1280px;
	position: relative;
	color: #000000;
	font-size: 14px;
	font-weight: 700;
	text-transform: uppercase;
	@include mq($max-width: 1199.98px) {
		position: fixed;
		top: 62px;
		right: 0;
		height: 100%;
		width: 100% !important;
		background: #fff;
		z-index: 120;
		display: block !important;
		overflow: hidden;
		height: 0px;
		opacity: 0;
		overflow-y: auto;
		font-size: 20px;
		@include transition(all 0.3s);
		&.active {
			display: block;
			height: calc(100vh - 62px);
			opacity: 1;
			@include mq($max-width: 767px) {
				padding-bottom: 50px;
			}
		}
	}

	ul {
		display: flex;
		justify-content: space-around;
		align-items: center;
		min-height: 47px;
		@include mq($max-width: 1199.98px) {
			display: block;
			padding: 15px;
		}
		li {
			// margin-left: 15px;
			position: relative;
			@include mq($max-width: 1199.98px) {
				display: block;
				margin-left: 10px;
				position: relative;
				margin-left: 0;
				padding-bottom: 0;
				border-bottom: 1px solid rgba(0, 0, 0, 0.3);
			}
			a {
				text-decoration: none;
				border-bottom: 3px solid transparent;
				padding-bottom: 20px;
				color: inherit;
				@include transition(all 0.5s);
				&:hover,
				&.active {
					border-bottom-color: #ffbe54;
					color: #537ffd;
					@include mq($max-width: 1199.98px) {
						border-bottom: none;
					}
				}
				@include mq($max-width: 1199.98px) {
					font-size: 18px;
					display: block;
					padding: 18px 18px 16px;
					margin: 0;
					border-bottom-width: 0px;
					border-bottom: none;
				}
			}
			&:hover {
				& > a {
					color: #537ffd;
				}
				.navigation-dropdown {
					display: block;
					@include mq($max-width: 1199.98px) {
						display: none;
					}
				}
			}
		}
		.navigation__lang {
			min-width: 57px;
			margin-left: 20px;
			padding: 8px;
			border: solid 1px rgba(0, 0, 0, 0.11);
			background-color: #f9f9f9;
			border-radius: 3px;
			@include mq($max-width: 1199px) {
				margin-left: 0;
				padding: 0;
				border: none;
				border-bottom: 1px solid rgba(0, 0, 0, 0.3);
				border-radius: 0px;
				background: none;
			}
			a span {
				display: none;
				@include mq($max-width: 1199px) {
					display: inline-block;
				}
			}
			img {
				padding-right: 5px;
			}
			li {
				font-size: 14px;
				font-weight: 300;
				letter-spacing: 0.7px;
				color: #414141;
				text-transform: uppercase;
			}
			.navigation-dropdown {
				left: -194px;
				top: 8px;
			}
		}
	}
	&__button {
		padding: 8px 0;
		text-align: right;
		font-size: 1.7rem;
		float: right;
		background: none;
		border: none;
		display: none;
		@include mq($max-width: 1199px) {
			display: block;
		}
	}
	&__close {
		display: none;
		position: absolute;
		top: 10px;
		right: 10px;
		font-size: 1.7rem;
		@include mq($max-width: 1199px) {
			display: block;
		}
	}
	&__overlay {
		display: none;
		&.active {
			background: #fff;
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 99;
			opacity: 1;
		}
	}

	// CTA buttons in mobile menu
	&__item--mobile {
		display: none;
		@include mq($max-width: 576px) {
			display: block;
		}
	}
	&__item--desktop {
		display: block;
		@include mq($max-width: 576px) {
			display: none;
		}
	}
	&__item-button {
		border-bottom: none !important;
		a {
			display: block;
			margin: 12px auto !important;
			padding: 15px 20px !important;
			max-width: 400px;
			font-size: 18px;
		}
	}
}

.navigation-dropdown {
	position: absolute;
	top: 0px;
	left: 0;
	padding-top: 40px;
	display: none;
	ul {
		width: 250px;
		max-height: calc(100vh - 100px);
		overflow: hidden auto;
		background: #fff;
		border: 1px solid #000;
		display: block;
		flex-wrap: wrap;
		padding: 20px 0;
		li {
			width: 100%;
			padding: 5px 20px;
			a {
				padding-bottom: 0px;
			}
		}
	}

	@include mq($max-width: 1199px) {
		position: static;
		padding-top: 0px;
		display: block !important;
		overflow: hidden;
		max-height: 0px;
		opacity: 0;
		@include transition(all 0.3s);

		&.active {
			display: block;
			max-height: initial;
			opacity: 1;
		}
		ul {
			width: auto;
			max-height: none;
			padding: 10px;
			background: none;
			border-color: rgba(0, 0, 0, 0.3);
			border-bottom: none;
			li {
				padding: 0;
				&:last-child {
					border-bottom: none;
				}
				a {
					padding: 0.5em;
					padding-top: 0.7em;
				}
			}
		}
	}
	&__trigger {
		display: inline-block;
		cursor: pointer;
		&:before {
			font-weight: 900;
			font-family: 'Font Awesome 5 Free';
			-moz-osx-font-smoothing: grayscale;
			-webkit-font-smoothing: antialiased;
			display: inline-block;
			font-style: normal;
			font-variant: normal;
			text-rendering: auto;
			line-height: 1;
			content: '\f078';
			@include mq($max-width: 1199px) {
				display: none;
			}
		}
		i {
			display: none;
			@include mq($max-width: 1199px) {
				display: inline-block;
			}
		}
		@include mq($max-width: 1199px) {
			position: absolute;
			top: 1em;
			right: 0.5em;
		}
	}
}

.rtl {
	.header .navigation-dropdown__trigger {
		right: unset;
		left: 0.5em;
	}
	i.flag:not(.icon) {
		margin: 0 0 0.5em 0;
	}
}
