.hero {
	position: relative;
	@media (max-width: 991.98px) {
		text-align: center;
	}
	&--image-bg {
		background-size: cover;
		background-position: center;
	}
	&--overlay {
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(30, 36, 62, 0.7);
		}
	}
	&__ghost-text {
		position: absolute;
		top: 50%;
		left: 2%;
		transform: translateY(-50%);
		color: rgba(255, 255, 255, 0.05);
		font-size: 240px;
		line-height: 212px;
		font-weight: 700;
		text-align: center;
		@include mq($max-width: 991.98px) {
			font-size: 20vw;
			line-height: 1;
		}
	}
	&__image-bg {
		background-size: cover;
		background-position: center;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
		z-index: -1;
	}
	&__text {
		font-size: 20px;
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: $gap;
		@media (max-width: 991.98px) {
			align-items: center;
		}
	}
	&__buttons {
		display: flex;
		flex-wrap: wrap;
		gap: $gap;
		@media (max-width: 991.98px) {
			justify-content: center;
		}
		a {
			min-width: auto;
		}
	}
	&__img {
		max-width: 100%;
		height: auto;
	}
}
