.video {
	padding: 53px 0;
	text-align: center;
	&__title {
		font-size: 48px;
		font-weight: 700;
		letter-spacing: 0.48px;
		text-align: center;
		padding-bottom: 50px;
		color: #212121;
		@include mq($max-width: 767px) {
			font-size: 25px;
			padding-bottom: 20px;
		}
	}
	&__img {
		display: inline-block;
		position: relative;
		img {
			display: block;
			position: relative;
			z-index: 2;
		}
		&:before {
			content: '';
			position: absolute;
			top: 11px;
			right: -8px;
			background-color: #fbcd04;
			height: 100%;
			width: 100%;
		}
		&:after {
			background: url(../img/play.png) no-repeat center;
			width: 132px;
			height: 132px;
			position: absolute;
			left: 50%;
			top: 50%;
			margin-top: -66px;
			margin-left: -66px;
			content: '';
			z-index: 3;
			@include transition(all 0.5s);
		}
		&:hover {
			&:after {
				transform: scale(1.2);
			}
		}
		&-index {
			img {
				border-radius: 3px;
			}
			&:before {
				background-image: linear-gradient(to right, #42cde8, #547cfe);
				border-radius: 3px;
			}
			&:after {
				background: url(../img/play-blue.png) no-repeat center;
			}
		}
	}
}
