@use 'sass:map';
@use 'sass:meta';

@mixin povh($povh) {
	position: relative;
	overflow: hidden;
}

@mixin heading-base() {
	line-height: 1.25;
	font-weight: 700;
	color: $heading-color;
	font-family: $font-family-secondary;
}

@mixin button-base() {
	padding: $button-padding-vertical $button-padding-horizontal;
	display: inline-block;
	// min-width: 256px;
	font-family: $font-family-secondary;
	font-size: $button-font-size;
	font-weight: 700;
	line-height: $button-line-height;
	border-radius: $button-border-radius;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	transition: $button-transition;
	appearance: none;
	outline: none;
}

@mixin button-variant($bg-color, $text-color: $button-text-color) {
	@include button-base();
	background-color: $bg-color;
	color: $text-color;
	border: 1px solid $bg-color;
	&:hover {
		background-color: hover-color($bg-color);
		border-color: hover-color($bg-color);
		color: $text-color;
	}
	&:disabled {
		opacity: $button-disabled-opacity;
	}
}

@mixin button-outline-variant($bg-color, $border-color, $hover-color: $button-outline-hover-color, $text-color: $button-outline-text-color) {
	@include button-base();
	background-color: $bg-color;
	color: $text-color;
	border: 1px solid $border-color;
	&:hover {
		border-color: $hover-color;
		background-color: $bg-color;
		color: $hover-color;
	}
	&:disabled {
		opacity: $button-disabled-opacity;
	}
}

@mixin input($input) {
	width: 300px;
	height: 39px;
	box-sizing: border-box;
	padding: 0 10px;
	color: #858585;
	font-size: 13px;
	font-family: $font-family;
	border: none;
	border-top: 1px solid #e0e7e9;
	border-bottom: 1px solid #e0e7e9;
	border-radius: 1px;
	resize: none;
	background: #ffffff;
}
@mixin input_focus($input_focus) {
	box-shadow: 0 0 2px #99e6f9;
}

@mixin input-new($input-new) {
	height: 40px;
	padding: 8px 12px;
	appearance: none;
	border: 1px solid $border-color;
	width: 100%;
	max-width: 600px;
	border-radius: 6px;
	background-color: #fff;
	&::placeholder {
		color: #838383;
	}
}
@mixin input-new_focus($input-new_focus) {
	border-color: $color-primary;
}

@mixin cover {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%;
}

// TRANSITION
@mixin transition($what) {
	& {
		transition: $what;
	}
}

@mixin hide-text {
	font: 0 / 0 a;
	text-shadow: none;
	color: transparent;
}

//triangle
@mixin arr($width, $height, $bg, $direction) {
	width: 0px;
	height: 0px;
	border-style: solid;
	@if $direction==t {
		border-width: 0 calc($width / 2) + px $height + px calc($width / 2) + px;
		border-color: transparent transparent $bg transparent;
	}
	@if $direction==r {
		border-width: calc($height / 2) + px 0 calc($height / 2) + px $width + px;
		border-color: transparent transparent transparent $bg;
	}
	@if $direction==b {
		border-width: $height + px calc($width / 2) + px 0 calc($width / 2) + px;
		border-color: $bg transparent transparent transparent;
	}
	@if $direction==l {
		border-width: calc($height / 2) + px $width + px calc($height / 2) + px 0;
		border-color: transparent $bg transparent transparent;
	}
	@if $direction==tl {
		border-width: $height + px $width + px 0 0;
		border-color: $bg transparent transparent transparent;
	}
	@if $direction==tr {
		border-width: 0 $width + px $height + px 0;
		border-color: transparent $bg transparent transparent;
	}
	@if $direction==br {
		border-width: 0 0 $height + px $width + px;
		border-color: transparent transparent $bg transparent;
	}
	@if $direction==bl {
		border-width: $width + px 0 0 $height + px;
		border-color: transparent transparent transparent $bg;
	}
}
// example
// +arr(500,50, #000, l)
// +arr(500,50, #000, bl)

// Mixin to generate spacing classes
@mixin generate-spacing-classes($name, $prefix) {
	@each $key, $value in $spacing-scale {
		.#{$prefix}-#{$key} {
			#{$name}: #{$value};
		}
		.#{$prefix}t-#{$key} {
			#{$name}-top: #{$value};
		}
		.#{$prefix}r-#{$key} {
			#{$name}-right: #{$value};
		}
		.#{$prefix}b-#{$key} {
			#{$name}-bottom: #{$value};
		}
		.#{$prefix}l-#{$key} {
			#{$name}-left: #{$value};
		}
		.#{$prefix}x-#{$key} {
			#{$name}-left: #{$value};
			#{$name}-right: #{$value};
		}
		.#{$prefix}y-#{$key} {
			#{$name}-top: #{$value};
			#{$name}-bottom: #{$value};
		}
	}
}

// Mixin to generate padding classes with media queries
@mixin generate-padding-classes() {
	@include generate-spacing-classes('padding', 'p');
}

// Mixin to generate margin classes with media queries
@mixin generate-margin-classes() {
	@include generate-spacing-classes('margin', 'm');
}

@mixin mq($args...) {
	$media-type: 'only screen';
	$media-type-key: 'media-type';
	$args: meta.keywords($args);
	$expr: '';

	@if map.has-key($args, $media-type-key) {
		$media-type: map.get($args, $media-type-key);
		$args: map.remove($args, $media-type-key);
	}

	@each $key, $value in $args {
		@if $value {
			$expr: '#{$expr} and (#{$key}: #{$value})';
		}
	}

	@media #{$media-type} #{$expr} {
		@content;
	}
}
