.color-blue {
	color: #3e63f5;
}
.pricing-section {
	padding-top: 40px;
	padding-bottom: 40px;
	background: #eef1f7;
	color: #333333;
	@include mq($max-width: 767px) {
		padding-top: 30px;
		padding-bottom: 30px;
	}
	.pricing-section__title {
		padding-bottom: 40px;
		font-size: 42px;
		font-weight: 500;
		color: #303030;
		line-height: normal;
		@include mq($max-width: 767px) {
			font-size: 32px;
			padding-bottom: 30px;
		}
		&--inverse {
			color: #fff;
		}
	}
}
.pricing-hero {
	padding-top: 70px;
	padding-bottom: 80px;
	background: #eef1f7;
	@include mq($max-width: 991px) {
		padding-top: 50px;
		padding-bottom: 50px;
		text-align: center;
	}
	@include mq($max-width: 576px) {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	h1 {
		padding-top: 65px;
		padding-bottom: 25px;
		font-size: 48px;
		font-weight: 700;
		line-height: normal;
		color: #333;
		@include mq($max-width: 767px) {
			font-size: 36px;
			padding-top: 30px;
		}
	}
	p {
		padding-bottom: 50px;
		font-size: 20px;
		font-weight: 400;
		line-height: normal;
		color: #787878;
	}
	&__img {
		@include mq($max-width: 991px) {
			margin-left: auto;
			margin-right: auto;
			display: block;
			max-width: 500px;
			width: 100%;
		}
	}
	.button-orange {
		min-width: auto;
		margin-bottom: 20px;
	}
}
.pricing-plans {
	padding-bottom: 30px;
	line-height: normal;
	background-color: #eef1f7;
	@include mq($max-width: 991px) {
		padding-bottom: 15px;
	}
	@include mq($max-width: 991px) {
		padding-bottom: 0px;
	}
}
.pricing-filters {
	&__info {
		margin-bottom: 20px;
		font-size: 18px;
		font-style: italic;
		svg {
			margin-left: 5px;
			display: inline;
			line-height: 1;
			vertical-align: -5px;
		}
	}
	&__popup-btn {
		text-decoration: none;
	}
}
.subscription-length {
	margin-bottom: 20px;
	display: flex;
	&__item {
		position: relative;
		height: 60px;
		width: 100%;
		max-width: 280px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 18px;
		font-weight: 500;
		color: #2e2e2e;
		background: #fff;
		cursor: pointer;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		&.active {
			background: #58a4fd;
			color: #fff;
		}
		&:not(.active):hover {
			color: #58a4fd;
		}
		&:first-child {
			border-radius: 12px 0px 0px 12px;
		}
		&:last-child {
			border-radius: 0px 12px 12px 0px;
		}
	}
	&__label {
		position: absolute;
		right: -7px;
		top: -10px;
		padding: 5px 10px;
		border-radius: 32px;
		background: #ffee53;
		color: #2e2e2e;
		font-size: 16px;
		font-weight: 700;
	}
}

.pricing-size {
	margin-bottom: 20px;
	height: calc(100% - 20px);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	&__item {
		margin-bottom: 8px;
		padding: 8px 40px 8px 20px;
		position: relative;
		font-size: 16px;
		font-weight: normal;
		border-radius: 12px;
		border: 2px solid #fff;
		cursor: pointer;
		background-color: #fff;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		transition: all 0.3s ease;
		&.active {
			border-color: #58a4fd;
			color: #58a4fd;
			.pricing-size__icon {
				opacity: 1;
			}
		}
		&:hover .pricing-size__icon {
			opacity: 0.5;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__icon {
		position: absolute;
		right: 20px;
		top: 50%;
		transform: translateY(-50%);
		font-size: 24px;
		color: #58a4fd;
		opacity: 0;
		transition: all 0.3s ease;
	}
	&__name {
		padding-bottom: 5px;
		font-size: 24px;
		font-weight: 500;
	}
	&__amount {
		font-size: 16px;
	}
}
.pricing-features {
	margin-bottom: 20px;
	height: calc(100% - 20px);
	display: flex;
	flex-direction: column;
	&__item {
		flex: 1;
		padding: 15px 10px;
		display: flex;
		align-items: center;
		background: #fff;
		font-size: 16px;
		&:nth-child(odd) {
			background: #f2f8ff;
		}
		&:first-child {
			border-radius: 12px 12px 0 0;
		}
		&:last-child {
			border-radius: 0 0 12px 12px;
		}
	}
	&__icon {
		flex-shrink: 0;
		padding-right: 10px;
	}
	.list-tooltip {
		margin-left: auto;
		padding-left: 5px;
		padding-right: 5px;
		position: relative;
		display: inline-block;
		flex-shrink: 0;
		img {
			display: block;
		}

		&__icon:hover + .list-tooltip__text {
			opacity: 1;
			visibility: visible;
		}

		&__text {
			padding: 10px 12px;
			position: absolute;
			top: 50%;
			transform: translate3d(0, -50%, 0);
			left: calc(100% + 8px);
			z-index: 1;
			opacity: 0;
			visibility: hidden;
			width: 200px;
			background-color: #fff;
			font-size: 13px;
			font-weight: normal;
			line-height: 1.3;
			text-align: initial;
			color: #333;
			border-radius: 6px;
			// box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.14);
			-webkit-filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.14));
			filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.14));
			transition: opacity 0.3s ease;
			&:after {
				content: '';
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 100%;
				border-width: 5px;
				border-style: solid;
				border-color: transparent #fff transparent transparent;
			}
			// tooltip bottom
			@include mq($max-width: 991px) {
				left: unset;
				right: 30px;
				&:after {
					right: unset;
					left: 100%;
					border-color: transparent transparent transparent #fff;
				}
			}
		}
	}
}
.pricing-plan {
	margin-bottom: 20px;
	height: calc(100% - 20px);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding: 20px;
	border-radius: 12px;
	background: #fff;
	font-size: 14px;
	text-align: center;
	&__header {
		padding-bottom: 10px;
		border-bottom: 2px solid #ededed;
	}
	&__name {
		padding-bottom: 10px;
		font-size: 24px;
		font-weight: 600;
	}
	&__size {
		font-size: 18px;
	}
	&__body {
		padding: 15px 0;
	}
	&__subscription-price {
		padding-bottom: 10px;
		font-size: 46px;
		font-weight: 700;
		@include mq($max-width: 576px) {
			font-size: 40px;
		}
	}
	&__plus {
		padding: 5px 0;
		font-size: 36px;
		font-weight: 500;
	}
	&__implementation-price {
		padding-bottom: 10px;
		font-size: 36px;
		font-weight: 500;
		@include mq($max-width: 576px) {
			font-size: 32px;
		}
	}
	&__button {
		min-width: auto;
		width: 100%;
		margin-bottom: 20px;
	}
}
.pricing-support {
	@include mq($max-width: 767px) {
		font-size: 14px;
	}
	&__btn {
		margin-top: 15px;
	}
	&__item {
		margin-bottom: 25px;
		padding: 30px 15px;
		height: calc(100% - 25px);
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		gap: 30px;
		align-self: stretch;
		font-size: 14px;
		color: #3a3a3a;
		border-radius: 12px;
		background: #fff;
		h3 {
			font-size: 24px;
			font-weight: 700;
			margin-bottom: 20px;
			@include mq($max-width: 767px) {
				font-size: 18px;
			}
		}
	}

	&__icon {
		max-width: 60px;
		max-height: 60px;
	}
}
.pricing-addons {
	overflow: hidden; // contain horizontal overflow within the section
	&__wrapper {
		padding-top: 40px;
		padding-bottom: 40px;
		background: #305e93;
		@include mq($max-width: 767px) {
			padding-top: 30px;
			padding-bottom: 30px;
		}
	}
	&__wrapper &__slider {
		// Left ghost slide
		.swiper-slide.previous-slide {
			opacity: 30%;
			visibility: visible;
			position: relative;
			filter: blur(5px);
			&:before {
				position: absolute;
				content: '';
				width: 100%;
				height: 100%;
				background-image: linear-gradient(to left, transparent, #305e93);
			}
		}
		// Right ghost slide
		.swiper-slide.next-slide {
			opacity: 30%;
			visibility: visible;
			position: relative;
			filter: blur(5px);
			&:before {
				position: absolute;
				content: '';
				width: 100%;
				height: 100%;
				background-image: linear-gradient(to right, transparent, #305e93);
			}
		}
	}
	&__slider {
		&.swiper {
			width: 100%;
			overflow: visible; // show ghost slides
		}
		// Custom navigation buttons (no Swiper default classes)
		.swiper-arrow {
			position: absolute;
			top: calc(50% - 28px);
			z-index: 10;
			cursor: pointer;
			width: 56px;
			height: 56px;
			border-radius: 50%;
			background: #fff url(../img/icons/arrow.png) no-repeat 55% center;
			box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
			transition: all 0.3s ease;
			@include hide-text;
			&--prev {
				transform: scaleX(-1);
				transform-origin: center;
				left: -5px;
				@include mq($min-width: 567px) {
					left: -12px;
				}
				@include mq($min-width: 1520px) {
					left: -75px;
				}
			}
			&--next {
				right: -5px;
				@include mq($min-width: 567px) {
					right: -12px;
				}
				@include mq($min-width: 1520px) {
					right: -75px;
				}
			}
		}
		// Ensure slides stretch to equal height
		.swiper-wrapper {
			display: flex;
			align-items: stretch;
		}
		.swiper-slide {
			box-sizing: border-box;
			height: auto;
			transition: opacity 0.3s ease, filter 0.3s ease;
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			& > div {
				height: 100%;
			}
		}
		.swiper-slide.swiper-slide-visible,
		.swiper-slide.previous-slide,
		.swiper-slide.next-slide {
			opacity: 1;
			visibility: visible;
			pointer-events: auto;
		}
	}
	&__slide {
		padding: 0 10px;
		color: #333;
		font-size: 14px;
		height: 100%;
	}
	&__slide-container {
		padding: 0px 15px 30px 15px;
		height: 100%;
		border-radius: 12px;
		background: #fff;
	}
	&__slide-image {
		width: 100%;
		display: block;
		margin-bottom: 10px;
		img {
			width: 100%;
		}
	}
	&__slide-title {
		margin-bottom: 20px;
		font-size: 24px;
		font-weight: 700;
	}
}
.pricing-process {
	&__title {
		margin-bottom: 20px;
		font-size: 36px;
		font-weight: 500;
		color: #303030;
		line-height: normal;
		@include mq($max-width: 767px) {
			font-size: 28px;
		}
	}
	&__col:not(:last-child) {
		@include mq($max-width: 991px) {
			margin-bottom: 30px;
		}
	}
	&__list {
		counter-reset: step;
	}
	&__list-item {
		border-radius: 12px;
		background: #fff;
		display: flex;
		padding: 20px 15px;
		align-items: flex-start;
		gap: 20px;
		align-self: stretch;
		&::before {
			counter-increment: step;
			content: counter(step);
			width: 53px;
			height: 53px;
			flex: 0 0 auto;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 24px;
			color: #333;
			font-style: italic;
			font-weight: 500;
			line-height: 1;
			border-radius: 50px;
			border: 2px solid #58a4fd;
			background: #fff;
			@include mq($max-width: 576px) {
				width: 45px;
				height: 45px;
			}
		}
		&:not(:last-child) {
			margin-bottom: 10px;
		}
		&--border-yellow::before {
			border-color: #ffe58a;
		}
	}
}
.pricing-accordion {
	&__item {
		margin-bottom: 10px;
		&.active {
			.pricing-accordion__icon {
				transform-origin: center;
				transform: rotate(180deg) translateY(50%);
			}
		}
	}
	&__trigger {
		padding: 20px 40px 20px 20px;
		position: relative;
		border-radius: 12px;
		background: #fff;
		cursor: pointer;
		h3 {
			color: #333;
			font-size: 18px;
		}
	}
	&__icon {
		position: absolute;
		right: 20px;
		top: 50%;
		transform: translateY(-50%);
		font-size: 18px;
		line-height: 1;
		@include transition(all 0.3s ease);
	}
	&__content {
		display: none;
		padding-top: 20px;
		font-size: 16px;
		color: #333;
		p {
			padding-bottom: 10px;
		}
		@include mq($max-width: 991px) {
			font-size: 14px;
		}
	}
}
.pricing-meeting {
	padding-bottom: 0;
	&__wrapper {
		padding-top: 20px;
		padding-bottom: 20px;
		background: #fff;
		@include mq($max-width: 991px) {
			padding-top: 40px;
			padding-bottom: 40px;
			text-align: center;
		}
	}
	h2 {
		padding-bottom: 20px !important;
		@include mq($max-width: 768px) {
			text-align: center;
		}
	}
	p {
		padding-bottom: 15px;
		font-size: 18px;
		line-height: 1.5;
		color: #4d4d4d;
	}
	.button-orange {
		margin-top: 20px;
	}
	img {
		@include mq($max-width: 768px) {
			padding-bottom: 20px;
		}
	}
}
.core-modal {
	max-width: 360px;
	padding: 0;
	text-align: center;
	cursor: default !important;
	font-size: 16px;
	line-height: 1.5;
	color: #333;
	border-radius: 12px;
	&__header {
		height: 44px;
		background-color: #58a4fd;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 12px 12px 0 0;
		font-size: 20px;
		p {
			font-size: 15px;
			font-weight: 500;
			letter-spacing: 0.38px;
			color: #fffefe;
		}
	}
	&__content {
		padding: 20px 25px 20px;
		border-radius: 0 0 12px 12px;
	}
	&__close {
		padding: 12px 24px;
		display: inline-block;
		background: #5677f6;
		background: #58a4fd;
		color: #fff;
		border-radius: 12px;
		text-decoration: none;
		line-height: 1;
	}
}
