.accordion {
	margin-bottom: $gap;
	&:last-child {
		margin-bottom: 0;
	}
	&__header {
		position: relative;
		min-height: 64px;
		padding: 16px 20px;
		display: flex;
		align-items: center;
		cursor: pointer;
		background-color: #fff;
		border-radius: 12px;
		&:after {
			margin-left: auto;
			font-weight: 900;
			font-family: 'Font Awesome 5 Free';
			-moz-osx-font-smoothing: grayscale;
			-webkit-font-smoothing: antialiased;
			display: inline-block;
			font-style: normal;
			font-variant: normal;
			text-rendering: auto;
			line-height: 1;
			content: '\f078';
			transform-origin: center;
			transition: transform 0.3s;
		}
		&.active {
			&:after {
				transform: rotate(180deg);
			}
		}
	}
	&__title {
		font-size: 18px;
		font-weight: 400;
		line-height: 1.5;
	}
	&__content {
		margin-top: 20px;
		font-size: 16px;
		line-height: 1.5;
		display: none;
	}

	// Base styles
	&.accordion--base &__header {
		background-color: unset;
		min-height: auto;
		padding: 8px 0;
	}
	&.accordion--base &__content {
		background-color: unset;
		min-height: auto;
		margin-top: 8px;
	}
	&.accordion--base &__title {
		@extend .h3;
	}
}
