.calendly-main {
	padding-top: 70px;
	padding-bottom: 50px;
	font-size: 18px;
	color: #3d3d3d;
	height: 100%;
	h1 {
		font-size: 48px;
		font-weight: 500;
		letter-spacing: 1.2px;
		padding-bottom: 30px;

		@include mq($max-width: 991px) {
			font-size: 42px;
		}

		@include mq($max-width: 767px) {
			font-size: 36px;
		}
	}
}

.calendly-content {
	padding-top: 50px;
}

.calendly-inline-widget {
	// max-width: 650px;
	// width: 100%;
	height: 720px;
	margin-left: auto;
	margin-right: auto;
}

.calendly-box {
	margin-bottom: 20px;
	min-height: 320px;
	border-radius: 20px;
	background: #fff;
	color: #2c2c2c;
	box-shadow: 0px 2px 27px 0px rgba(0, 0, 0, 0.11);
}

.calendly-button {
	--icon-color: #2c2c2c;
	min-height: 150px;
	padding: 20px 30px;
	height: calc(100% - 20px);
	display: flex;
	align-items: center;
	color: #2c2c2c;
	cursor: pointer;
	transition: all 0.3s ease;
	&:hover {
		box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.16);
		color: #1a74c3;
	}
	&.active {
		--icon-color: #1a74c3;
	}
	&__icon {
		margin-right: 30px;
		width: 100px;
		height: 100px;
		flex-shrink: 0;
		transition: all 0.3s ease;
		@include mq($max-width: 767.98px) {
			width: 50px;
			height: 50px;
			margin: 0 0 8px;
		}
	}
	&__text {
		font-size: 20px;
		font-weight: 700;
		line-height: 1.5;
		text-transform: uppercase;
		flex: 1;
		@include mq($max-width: 767.98px) {
			font-size: 16px;
			display: flex;
			align-items: center;
			text-align: center;
		}
	}
	@include mq($max-width: 767.98px) {
		flex-direction: column;
		align-items: center;
	}
}

.calendly-form {
	overflow: hidden;
	&__content {
		display: none;
		&.active {
			display: block;
		}
	}
}

.calendly-placeholder {
	padding: 30px 0px;
	font-size: 24px;
	font-weight: 400;
	line-height: 30px;
	text-align: center;
	&__items {
		padding: 0 40px;
	}
	&__item {
		text-align: center;
		flex-basis: 50%;
	}
	figure {
		font-size: 20px;
		img {
			width: 100px;
			height: 100px;
			display: block;
			margin: 0 auto 25px;
		}
	}
	&__text {
		position: relative;
		margin-top: 40px;
		padding: 0 40px;
		font-weight: 700;
		&::before {
			position: absolute;
			content: url('../img/demo-arrow.svg');
			bottom: 4px;
			left: 8px;
			width: 80px;
			height: 40px;
			@include mq($max-width: 991.98px) {
				bottom: 20px;
			}
			@include mq($max-width: 767.98px) {
				bottom: unset;
				top: -15px;
				left: 0px;
				transform: rotate(45deg);
				transform-origin: right;
			}
			@include mq($max-width: 576px) {
				left: -20px;
			}
		}
	}
}

.calendly-widget {
	overflow: hidden;
}

.calendly-account {
	padding: 30px 30px 5px;
	form {
		width: 100%;
	}
	&__field {
		padding-bottom: 20px;
		td + td {
			padding-left: 20px;
		}
	}
	table input[type='radio'] + label {
		display: block;
	}

	input[type='radio'] {
		clip: rect(0 0 0 0);
		-webkit-clip-path: inset(100%);
		clip-path: inset(100%);
		height: 1px;
		overflow: hidden;
		position: absolute;
		white-space: nowrap;
		width: 1px;
		& + label {
			display: inline-block;
			padding: 15px;
			font-size: 16px;
			font-style: normal;
			font-weight: 400;
			border-radius: 40px;
			border: 1px solid #c9c9c9;
			background: #fff;
			cursor: pointer;
			text-align: center;
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
		}
		&:checked + label {
			border-color: #9fd2ff;
			background: #9fd2ff;
		}
	}
	input[type='text'],
	input[type='date'],
	input[type='email'],
	textarea {
		border-radius: 40px;
		border: 1px solid #c9c9c9;
		background: #fff;
		width: 100%;
	}
	textarea {
		border-radius: 18px;
	}
	label,
	.label {
		padding-bottom: 3px;
		color: #3f3f3f;
		font-size: 14px;
		font-weight: 400;
		line-height: normal;
	}
	&__submit {
		min-width: 260px;
	}
	&__field-wrapper {
		position: relative;
		.error-message {
			color: #d56455;
			font-size: 14px;
			line-height: 16px;
		}
	}
	&__field-wrapper {
		position: relative;
	}
	&__field-wrapper.valid,
	&__field-wrapper.invalid {
		&:after {
			position: absolute;
			right: 10px;
			top: 50%;
			transform: translateY(-50%);
			width: 18px;
			height: 18px;
			border-radius: 50%;
			background: #4bb543;
			content: '\f00c';
			line-height: 18px;
			text-align: center;
			font-weight: 900;
			font-family: 'Font Awesome 5 Free';
			font-size: 10px;
			color: #fff;
			pointer-events: none;
		}
		input[type='text'],
		input[type='date'],
		input[type='email'],
		textarea {
			padding-right: 36px;
		}
	}
	&__field-wrapper.invalid {
		&:after {
			background: #f74801;
			content: '\f00d';
		}
	}
	.iti--allow-dropdown .iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly])) .iti__selected-country-primary:hover,
	.iti--allow-dropdown
		.iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly]))
		.iti__selected-country:has(+ .iti__dropdown-content:hover)
		.iti__selected-country-primary,
	.iti--allow-dropdown
		.iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly]))
		.iti__selected-country:has(+ .iti__dropdown-content:hover)
		.iti__selected-country-primary {
		border-radius: 20px 0 0 20px;
	}
}

.rtl {
	.calendly-tags img {
		padding-left: 12px;
		padding-right: 0;
	}
}
