.check-before {
	&:before {
		display: block;
		flex: 0 0 auto;
		content: '';
		background: url(../img/icons/check-circle.svg) no-repeat center center / contain;
		width: 20px;
		height: 1.5rem;
	}
}

.list {
	line-height: 1.5;
	li {
		position: relative;
		padding-bottom: 16px;
		display: flex;
		gap: $gap-list-icon;
	}
	&--dot {
		li {
			&:before {
				position: relative;
				top: 4px;
				display: block;
				flex: 0 0 auto;
				width: 12px;
				height: 12px;
				content: '';
				background-color: $color-primary;
				border-radius: 50%;
			}
		}
	}
	&--circle-check {
		li {
			@extend .check-before;
		}
	}
	&--numbered {
		li {
			counter-increment: list-counter;
			&:before {
				display: block;
				flex: 0 0 auto;
				content: counter(list-counter);
				font-weight: 700;
			}
		}
	}
}

.circled-list {
	padding-bottom: 16px;
	line-height: normal;
	li {
		position: relative;
		padding-left: 36px;
		padding-bottom: 16px;
		&:before {
			left: 0;
			position: absolute;
			display: block;
			content: url(../img/icons/feature-circle-list-icon.png);
		}
	}
}

.rtl {
	.circled-list {
		padding-left: 0;
		padding-right: 36px;
		&:before {
			left: unset;
			right: 0;
		}
	}
}

.base-list {
	display: flex;
	flex-direction: column;
	gap: var(--list-gap, 12px);
	li {
		display: flex;
		gap: var(--list-icon-gap, 12px);

		&:before {
			display: block;
			flex: 0 0 auto;
			width: 18px;
			height: 18px;
		}
	}
	&--circle-check li {
		&:before {
			content: url(../img/icons/base-circle-check.svg);
		}
	}
	&--cancel li {
		&:before {
			content: url(../img/icons/base-cancel.svg);
		}
	}
}
