@use 'sass:color';

.customer-stories-hero {
	padding-top: 240px;
	padding-bottom: 240px;
	background-image: url('../img/customer-stories-hero.jpeg');
	@include mq($max-width: 991.98px) {
		padding-top: 160px;
		padding-bottom: 160px;
	}
}

.client-filters {
	&__box {
		margin-bottom: 12px;
	}
	&__input {
		position: relative;
		input:not([type='checkbox'], [type='radio']),
		select {
			min-width: 200px;
			@include input-new('');
			&:focus,
			&:active,
			&:target {
				@include input-new_focus('');
				box-shadow: none !important;
			}
		}

		&--select,
		&--search {
			input:not([type='checkbox'], [type='radio']),
			select {
				padding-right: 36px !important;
			}
			&:before {
				position: absolute;
				top: 50%;
				right: 12px;
				font-family: 'Font Awesome 5 Free';
				font-size: 12px;
				font-weight: 900;
				color: #858585;
				transform: translateY(-50%);
				z-index: 1;
			}
		}
		&--select:before {
			content: '\f078';
		}
		&--search:before {
			content: '\f002';
		}
	}
}

.client-list {
	margin-top: 32px;
	&__item {
		margin-bottom: 16px;
	}
	@include mq($max-width: 767.98px) {
		margin-top: 16px;
	}
}

.client-card {
	display: flex;
	flex-direction: column;
	position: relative;
	min-height: 260px;
	height: 100%;
	border-radius: 8px;
	background-color: #ffffff;
	overflow: hidden;
	cursor: pointer;
	text-decoration: none;
	&:hover,
	&:target {
		.client-card__read-more {
			color: color.adjust($color-primary, $lightness: -10%);
			padding-left: 8px;
		}
		.client-card__background-img img {
			transform: scale(1.05);
		}
	}

	&__background-img {
		position: absolute;
		left: 0;
		right: 0;
		width: 100%;
		height: 100%;
		// Overlay
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, 0.5);
		}
		img {
			object-fit: cover;
			position: absolute;
			left: 0;
			right: 0;
			width: 100%;
			height: 100%;
			transition: all 0.3s linear;
			filter: blur(2px);
		}
	}
	&__content {
		position: relative;
		padding: 16px 20px;
		display: flex;
		flex-direction: column;
		flex: 1 0 auto;
	}
	&__body {
		flex: 1 0 auto;
	}
	&__footer {
		flex: 0;
	}
	&__logo {
		max-width: 100%;
		max-height: 120px;
		display: block;
	}
	&__title {
		font-size: 16px;
		font-weight: 700;
		line-height: 1.125;
		color: #fff;
		padding-bottom: 8px;
	}

	&__read-more,
	&__link {
		display: inline-flex;
		align-items: center;
		font-size: 12px;
		font-weight: 700;
		color: $color-primary;
		transition: all 0.3s ease-in-out;
		&:before {
			// Arrow right
			content: '\f061';
			font-family: 'Font Awesome 5 Free';
			font-size: 12px;
			line-height: 1;
			font-weight: 700;
			margin-right: 8px;
		}
	}
}
