.tile {
	padding: 16px;
	background: #fff;
	border-radius: 12px;
	gap: 12px;
	display: flex;
	flex-direction: column;
	height: 100%;
	&__title-wrapper {
		display: flex;
		align-items: center;
		gap: 12px;
	}
	&__title {
		font-size: 20px;
		font-weight: 700;
	}
	&--row {
		flex-direction: row;
	}
	&--large {
		gap: 32px;
		padding: 32px 16px;
	}
	&--check-before {
		flex-direction: row;
		@extend .check-before;
		gap: $gap-list-icon;
	}
}

.card {
	@extend .tile;
	--card-space-block: 20px;
	--card-space-inline: 16px;
	gap: 20px;
	font-size: 14px;
	&--p-vertical-small {
		--card-space-block: 16px;
	}
	&--gap-small {
		gap: 16px;
	}
	&--no-padding {
		padding: 0;
	}
	&--text-medium {
		font-size: 16px;
	}
	&--text-large {
		font-size: 18px;
	}
	&--text-small {
		font-size: 12px;
	}
	&__header {
		padding: var(--card-space-block) var(--card-space-inline) 0;
		&--no-padding {
			padding: 0;
			.card__image--full img {
				border-radius: 12px 12px 0 0;
			}
		}
	}
	&__body {
		padding: 0 var(--card-space-inline);
		&--no-padding {
			padding: 0;
		}
	}
	&__grid {
		display: flex;
		flex-direction: column;
		gap: 16px;
	}
	&__footer {
		padding: 0 var(--card-space-inline) var(--card-space-block);
		&--no-padding {
			padding: 0;
		}
	}
	&__image {
		img {
			max-width: 100%;
			height: auto;
			border-radius: 12px;
			object-fit: cover;
			display: inline-block;
		}
		&--full {
			width: 100%;
			img {
				width: 100%;
			}
		}
	}
	&__title {
		@extend .h4;
	}
	&__subtitle {
		@extend .h6;
		font-weight: 600;
	}
}

.plain-card {
	&__icon {
		width: 48px;
		height: 48px;
		margin-bottom: 24px;
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: 12px;
	}
}

.cta-card {
	border-radius: 24px;
	background-color: $color-dark;
	padding: 24px;
	display: flex;
	gap: 32px;
	color: #fff;
	font-size: 20px;
	line-height: 26px;
	font-weight: 700;
	font-family: $font-family-secondary;
	@media (min-width: 768px) {
		padding: 32px;
	}
	@media (min-width: 992px) {
		padding: 42px;
		flex-wrap: nowrap;
	}
	&__image {
		border-radius: 20px;
		overflow: hidden;
		align-self: start;
		img {
			display: block;
			width: 100%;
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: 8px;
	}
	&__title {
		color: #fff;
	}
	&__actions {
		margin-top: 32px;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		gap: 24px;
		@media (min-width: 992px) {
			margin-top: auto;
		}
	}
	&__link {
		color: #fff;
		font-family: $font-family-secondary;
		font-size: 20px;
		line-height: 26px;
		font-weight: 700;
		&:hover {
			color: #fff;
			opacity: 0.7;
			transition: all 0.3s ease;
		}
	}
}

.base-cta-card {
	min-height: 350px;
	padding: 24px;
	color: #fff;
	font-size: 20px;
	line-height: 26px;
	font-weight: 700;
	font-family: $font-family-secondary;
	border-radius: 24px;
	background-color: $color-dark;
	background-image: repeating-linear-gradient(to right, #5b5b5b 0, #5b5b5b 1px, transparent 1px, transparent 33%),
		repeating-linear-gradient(to bottom, #5b5b5b 0, #5b5b5b 1px, transparent 1px, transparent 33%);
	@media (min-width: 768px) {
		padding: 32px;
		background-image: repeating-linear-gradient(to right, #5b5b5b 0, #5b5b5b 1px, transparent 1px, transparent 25%),
			repeating-linear-gradient(to bottom, #5b5b5b 0, #5b5b5b 1px, transparent 1px, transparent 260px);
		background-position: 0 0, 0 46px;
	}
	@media (min-width: 992px) {
		padding: 42px;
	}
	&__title {
		margin-bottom: 24px;
		@extend .h1;
		color: #fff;
		@media (min-width: 992px) {
			font-size: 40px;
		}
		span {
			color: $color-primary;
		}
	}
}
