body,
html {
	scroll-behavior: smooth;
}
html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	padding: 0;
	padding-top: 61px;
	margin: 0;
	font-family: $font-family;
	font-size: $font-size;
	font-weight: normal;
	color: $text-color;
	line-height: 1.2;
}

input[type='submit'],
button {
	cursor: pointer;
	&::-moz-focus-inner {
		padding: 0;
		border: 0;
	}
}

input[type='text'],
input[type='email'],
input[type='tel'],
input[type='search'],
input[type='password'],
input[type='date'],
input[type='url'],
textarea {
	@include input('');
	&:focus {
		@include input_focus('');
	}
}

video::-webkit-media-controls {
	display: none;
}

input::-webkit-input-placeholder {
	color: inherit;
}
textarea::-webkit-input-placeholder {
	color: inherit;
}
input:-moz-placeholder {
	color: inherit;
}
textarea:-moz-placeholder {
	color: inherit;
}
textarea {
	height: 90px;
	padding-top: 5px;
}

a {
	color: $link-color;
	&:hover {
		color: $link-color-hover;
	}
}

img {
	@include mq($max-width: 992px) {
		max-width: 100%;
	}
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: inherit;
	font-weight: normal;
}
h1 {
	font-size: 24px;
}
h2 {
	font-size: 22px;
}
h3 {
	font-size: 20px;
}
h4 {
	font-size: 18px;
}
h5 {
	font-size: 16px;
}
h6 {
	font-size: 14px;
}
