.homepage {
	background-color: $bg-1;
	background-image: $bg-img;
	background-position-x: center;
	&__hero {
		padding-top: 60px;
		padding-bottom: 60px;
		@media (min-width: 576px) {
			padding-top: 100px;
			padding-bottom: 90px;
		}
		@media (min-width: 992px) {
			padding-top: 100px;
			padding-bottom: 90px;
		}
	}
}

.hero {
	&__main {
		@media (min-width: 992px) {
			padding-right: 60px;
		}
	}
	h1 {
		margin-top: 52px;
	}
	h2 {
		margin-top: 24px;
	}
	a {
		margin-top: 48px;
	}
	&__img,
	&__video {
		margin-top: 48px;
		max-width: 100%;
		height: auto;
		border-radius: $border-radius;
		@media (min-width: 992px) {
			margin-top: 0;
		}
	}
	&__video {
		width: 100%;
	}
}

.homepage-block {
	padding-bottom: 60px;
	@media (min-width: 992px) {
		padding-bottom: 120px;
	}
	&__header {
		text-align: center;
		margin-bottom: 48px;
		@media (min-width: 992px) {
			margin-bottom: 92px;
		}
		p {
			padding-top: 12px;
		}
	}
	&__subtitle {
		margin-top: 24px;
		font-size: 20px;
		font-weight: 700;
		font-family: $font-family-secondary;
	}
}

.case-card {
	border-radius: $border-radius;
	background-color: $card-bg;
	padding: 24px;
	gap: 32px;
	display: flex;
	flex-wrap: wrap;
	@media (min-width: 768px) {
		padding: 32px;
	}
	@media (min-width: 992px) {
		padding: 42px;
		gap: 40px;
		flex-wrap: nowrap;
	}
	&__tag {
		margin-bottom: 16px;
		display: block;
		font-weight: 400;
		font-size: 18px;
		line-height: 24px;
		color: $color-primary;
		text-transform: uppercase;
	}
	&__title {
		margin-bottom: 32px;
		@media (min-width: 768px) {
			margin-bottom: 48px;
		}
	}
	&__text {
		@extend .h4;
	}
	&__logo {
		flex: 0 0 auto;
		width: 120px;
	}
	&__action {
		flex: 0 0 auto;
	}
}

.pms-growth {
	display: flex;
	flex-direction: column;
	gap: 32px;
	@media (min-width: 768px) {
		flex-direction: row;
		gap: 48px;
	}
	&__path {
		flex: 0 0 auto;
		display: flex;
		align-items: center;
		justify-content: center;
		transform: rotate(90deg);
		@media (min-width: 768px) {
			padding-top: 112px;
			flex-direction: row;
			transform: none;
		}
	}
	&__path-icon {
		width: 52px;
		height: 52px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

.growth-card {
	&__header {
		display: flex;
		gap: 16px;
	}
	&__icon {
		background: $card-bg;
		border-radius: 16px;
		width: 72px;
		height: 72px;
		padding: 16px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__title {
		margin-bottom: 8px;
	}
	&__body {
		margin-top: 40px;
		background: $card-bg;
		border-radius: 16px;
		padding: 24px;
		@media (min-width: 992px) {
			padding: 40px;
		}
	}
	&__list {
		padding-top: 32px;
		--list-gap: 32px;
	}
}

.why-salesforce {
	text-align: center;
	@media (min-width: 768px) {
		text-align: initial;
	}
	&__row {
		display: flex;
		justify-content: center;
		@media (min-width: 768px) {
			justify-content: space-between;
		}
	}
	&__title {
		margin-bottom: 32px;
	}
	&__image {
		max-width: 100%;
		height: auto;
	}
}

.stock-cards {
	scroll-margin-top: 100px;
	position: relative;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 24px;
	@media (max-width: 576px) {
		display: flex;
		flex-direction: column;
		gap: 32px;
	}
	@media (max-width: 992px) {
		grid-template-columns: repeat(2, 1fr);
	}
	&__item {
		@media (max-width: 576px) {
			position: sticky;
			top: 92px;
		}
	}
}

.sf-card {
	padding: 24px;
	background-color: $card-bg;
	border-radius: 16px;
	min-height: 260px;
	background-image: url('../img/icons/sf-cloud.svg');
	background-repeat: no-repeat;
	background-position: right 24px bottom 24px;
	&__header {
		display: flex;
		align-items: center;
		gap: 12px;
		span {
			text-transform: uppercase;
			font-weight: 600;
			font-size: 12px;
			line-height: 20px;
			color: $strokes;
		}
	}
	&__icon {
		width: 44px;
		height: 44px;
		flex: 0 0 auto;
		border-radius: 8px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: $color-primary-light;
		img {
			max-width: 24px;
			max-height: 24px;
		}
	}

	&__body {
		margin-top: 24px;
		p {
			margin-top: 12px;
		}
	}
}

.solution-slider {
	overflow: hidden;
	.swiper {
		overflow: visible;
	}
	&__actions {
		position: relative;
		text-align: right;
		margin-top: 32px;
		@media (min-width: 768px) {
			margin-top: 70px;
		}
	}
	&__pagination {
		display: flex;
		justify-content: center;
		gap: 12px;
		pointer-events: none;
		margin-bottom: 32px;
		@media (min-width: 992px) {
			position: absolute;
			margin-bottom: 0;
		}
	}
	&__bullet {
		width: 32px;
		height: 4px;
		background-color: #96949a;
		@media (min-width: 768px) {
			width: 70px;
		}
		&--active {
			background-color: #333333;
		}
	}
	&__link {
		@media (min-width: 992px) {
			margin-right: 32px;
		}
	}
}
.solution-slide {
	height: 100%;
	width: 330px;
	display: flex;
	flex-direction: column;
	border-radius: 16px;
	background-color: $card-bg;
	&__cover {
		border-top-left-radius: 16px;
		border-top-right-radius: 16px;
		overflow: hidden;
		img {
			width: 100%;
		}
	}
	&__content {
		padding: 24px;
		min-height: 180px;
		display: flex;
		flex-direction: column;
		gap: 12px;
		align-items: flex-start;
		@media (min-width: 992px) {
			min-height: 230px;
		}
		.button {
			margin-top: auto;
		}
	}
}

.interactive-list {
	position: relative;
	display: flex;
	flex-direction: column;
	gap: 16px;
	@media (min-width: 768px) {
		flex-direction: row;
		gap: 24px;
	}
	@media (min-width: 992px) {
		gap: 90px;
	}
	&__list {
		li.active + li {
			border-top: none;
		}
		li {
			border-top: 1px solid $strokes;
			&:last-child {
				border-bottom: 1px solid $strokes;
			}
		}
	}
	&__content {
		@media (min-width: 768px) {
			width: 60%;
		}
	}
	&__all {
		padding: 24px;
		text-align: center;
	}
	&__content-card {
		display: none;
		border-radius: 24px;
		padding: 20px;
		backdrop-filter: blur(3px) saturate(110%);
		box-shadow: inset 2px 2px 1px 0 rgba(255, 255, 255, 0.5), inset -1px -1px 1px 1px rgba(255, 255, 255, 0.5);
		background-color: rgba(0, 0, 0, 0.01);
		&.active {
			display: block;
		}
		img {
			width: 100%;
			background-color: #fff;
			border-radius: 16px;
			display: block;
		}
	}
}

.interactive-card {
	&__trigger {
		padding: 24px;
		cursor: pointer;
		-webkit-tap-highlight-color: transparent;
		-webkit-focus-ring-color: rgba(255, 255, 255, 0);
	}
	&__content {
		display: none;
		padding-top: 12px;
		a {
			margin-top: 24px;
		}
	}

	&.active {
		padding: 24px;
		background: $color-dark;
		color: #fff;
		border-radius: 16px;
	}
	.active &__trigger {
		padding: 0;
		cursor: default;
		pointer-events: none;
	}
	.active &__content {
		display: block;
	}
	.active &__title {
		color: #fff !important;
	}
}

.arrow-link {
	color: $color-primary;
	font-family: $font-family-secondary;
	font-weight: 700;
	font-size: 18px;
	text-decoration: none;
	@media (min-width: 768px) {
		font-size: 20px;
	}
	i {
		margin-left: 8px;
	}
}

.app-integration-list {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -16px;
	justify-content: center;
	row-gap: 32px;
	&__item {
		flex: 0 1 auto;
		width: 20%;
		padding: 0 16px;
		justify-content: center;
		@media (max-width: 992px) {
			width: 33%;
		}
		@media (max-width: 768px) {
			width: 50%;
		}
		@media (max-width: 576px) {
			width: 100%;
		}
	}
}

.animated-line {
	position: relative;
	overflow: hidden;
	&__track {
		display: flex;
		align-items: center;
		width: fit-content;
		animation: scroll 40s linear infinite;
		will-change: transform;
	}
	&__group {
		display: flex;
		align-items: center;
		flex: 0 0 50%;
		width: 200%;
		img {
			display: block;
			padding-right: 76px;
			max-width: none;
		}
	}
}
@keyframes scroll {
	from {
		transform: translateX(0);
	}
	to {
		transform: translateX(-50%);
	}
}
