.footer {
	padding: 64px 0;
	flex-grow: 0;
	color: #96949a;
	background-color: $color-dark;
	@include povh('');
	&__top {
		padding-bottom: 24px;
	}
	&__bottom {
		padding-top: 24px;
		border-top: 1px solid $strokes;
	}
	&__row {
		--bs-gutter-y: 64px;
	}
	&__logo {
		display: inline-block;
		margin-bottom: 64px;
		@media (min-width: 768px) {
			margin-bottom: 72px;
		}
	}
	&__title {
		font-size: 18px;
		line-height: 24px;
		font-weight: 400;
		color: #abacad;
		text-transform: uppercase;
		min-height: 2lh;
	}
	&__nav {
		--bs-gutter-y: 64px;
	}
	&__list {
		margin-top: 16px;
		font-size: 16px;
		font-weight: 400;
		line-height: 20px;
		li:not(:last-child) {
			margin-bottom: 24px;
		}
		a {
			color: #fff;
			text-decoration: none;
			&:hover {
				color: #3e63f5;
			}
		}
	}
	&__badges {
		margin-top: 64px;
		justify-content: center;
		display: flex;
		gap: 16px;
		@media (min-width: 992px) {
			margin-top: 32px;
			justify-content: flex-end;
		}
		img {
			height: 40px;
			width: auto;
			@media (min-width: 576px) {
				height: 64px;
			}
		}
	}
	&__form {
		padding: 2px;
		background: #fff;
		position: relative;
		overflow: hidden;
		display: flex;
		margin-bottom: 12px;
		border-radius: $border-radius;
		border: 1px solid $strokes;
		height: 52px;
		input:not(.button) {
			width: 100%;
			height: 100%;
			border: none;
			background: none;
			box-shadow: none !important;
			flex: 1;
			padding: 0 24px;
			color: #545353;
			font-size: 14px;
			font-weight: 300;
			border-radius: $border-radius;
			@media (min-width: 576px) {
				font-size: 18px;
			}
		}
		.button {
			min-width: auto;
			border-radius: $border-radius;
		}
	}
	&__social {
		margin-top: 24px;
		color: #fff;
		display: flex;
		gap: 20px;
		@media (min-width: 992px) {
			margin-top: 8px;
		}
		a {
			color: #fff;
			font-size: 28px;
			text-decoration: none;
			&:hover {
				opacity: 0.75;
			}
		}
	}
	&__form-text {
		text-align: center;
		font-size: 12px;
	}
}
