.cta-section-2 {
	text-align: center;
	&__image {
		margin-bottom: 20px;
		@media (min-width: 768px) {
			margin-bottom: 0;
		}
	}
	&__title {
		padding-bottom: 20px;
	}
	&__text {
		font-size: 18px;
		padding-bottom: 20px;
		line-height: 1.33;
	}
	&__buttons a {
		min-width: auto;
		margin-top: 12px;
	}
	@media (min-width: 768px) {
		text-align: left;
	}
}

.cta-section {
	@extend .block;
	@extend .block--spaced;
	text-align: center;
	background-color: #fff;
	@media (min-width: 768px) {
		text-align: left;
	}
	&__grid {
		display: flex;
		flex-wrap: wrap;
		gap: $gap;
		align-items: center;
		justify-content: space-between;
		@media (min-width: 768px) {
			flex-wrap: nowrap;
		}
	}
	&__content,
	&__image {
		flex: 1 1 auto;
		@media (min-width: 768px) {
			flex: 1 1 50%;
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: $gap;
	}
	&__image {
		img {
			max-width: 100%;
			height: auto;
			display: block;
			@media (max-width: 991.98px) {
				max-height: 300px;
				margin-left: auto;
				margin-right: auto;
			}
		}
		@media (min-width: 768px) {
			order: 1;
		}
	}

	&__title {
		@extend .h2;
	}
	&__buttons {
		margin-top: 12px;
		display: flex;
		flex-wrap: wrap;
		gap: 12px;
		justify-content: center;
		a {
			min-width: auto;
		}
		@media (min-width: 992px) {
			justify-content: start;
		}
	}
}
