@use 'sass:color';

@function hover-color($base-color) {
	// Darken the color slightly for hover effect
	$darkened-color: color.adjust($base-color, $lightness: -7%);
	// Saturate the color slightly to make it more vibrant
	$hover-color: color.adjust($darkened-color, $saturation: 5%);
	@return $hover-color;
}

.button {
	@include button-variant($button-primary-bg);

	&-secondary {
		@include button-variant($button-secondary-bg);
	}

	&-whatsapp {
		@include button-variant($button-whatsapp-bg);
	}

	&-success {
		@include button-variant($button-success-bg);
	}

	&-danger {
		@include button-variant($button-danger-bg);
	}

	&-warning {
		@include button-variant($button-warning-bg);
	}

	&-outline {
		@include button-outline-variant($button-outline-bg, $button-outline-border-color, $button-outline-hover-color);
	}

	&-inverse {
		@include button-variant($button-inverse-bg, $button-inverse-text-color);
	}

	&--auto {
		min-width: auto;
	}

	&--stretch {
		width: 100%;
	}
}

