// Fixed whatsapp button
.whatsapp-button {
	background: $button-whatsapp-bg;
	padding: 10px 14px;
	border-radius: 40px;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	font-size: 14px;
	color: #fff;
	text-decoration: none;
	cursor: pointer;
	position: fixed;
	z-index: 999999;
	bottom: 20px;
	right: 20px;
	transition: $button-transition;
	@include mq($max-width: 991px) {
		display: none;
	}
	&:hover {
		color: #fff;
		background-color: hover-color($button-whatsapp-bg);
	}
	svg {
		margin-right: 8px;
	}
	&__mobile {
		display: none;
		@include mq($max-width: 991px) {
			display: inline-flex;
		}
	}
}
