.block {
	line-height: 1.5;
	&--spaced {
		padding-top: 24px;
		padding-bottom: 24px;
		@media (min-width: 992px) {
			padding-top: 32px;
			padding-bottom: 32px;
		}
	}
	&__title {
		padding-bottom: 24px;
		text-align: center;
		&--left {
			text-align: left;
		}
		&--right {
			text-align: right;
		}
	}
	&__sections {
		display: flex;
		flex-direction: column;
		gap: 48px;
		@media (min-width: 992px) {
			gap: 64px;
		}
	}
}

.section {
	&__grid {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: $gap;
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: $gap;
	}
	&__image {
		text-align: center;
		img {
			max-width: 100%;
			height: auto;
			display: inline-block;
			@media (max-width: 991.98px) {
				max-height: 400px;
				margin-left: auto;
				margin-right: auto;
			}
		}
		@media (max-width: 991.98px) {
			order: -1;
		}
	}
	&__content,
	&__image {
		width: 100%;
		@media (min-width: 992px) {
			width: calc(50% - $gap / 2);
		}
	}
	&--wide {
		.section__grid {
			display: flex;
			flex-direction: column;
			gap: $gap;
		}
		.section__content {
			width: 100%;
		}
		.section__image {
			@media (min-width: 992px) {
				width: 100%;
			}
			img {
				@media (max-width: 991.98px) {
					max-height: 100%;
				}
			}
		}
	}
	&--reversed {
		@media (min-width: 992px) {
			.section__grid {
				flex-direction: row-reverse;
			}
		}
	}
}

.blocks-wrapper {
	padding-top: 24px;
	padding-bottom: 24px;
	@media (min-width: 768px) {
		padding-top: 48px;
		padding-bottom: 48px;
	}
}
