.checkout-page {
	background: #eef1f7;
}
.checkout-hero {
	padding-top: 80px;
	padding-bottom: 50px;
	@include mq($max-width: 767px) {
		padding-top: 50px;
		text-align: center;
	}
	h1 {
		padding-bottom: 20px;
		color: #333;
		font-size: 46px;
		font-style: normal;
		font-weight: 700;
		line-height: normal;
		@include mq($max-width: 767px) {
			font-size: 36px;
		}
	}
	p {
		color: #787878;
		font-size: 20px;
		font-weight: 500;
	}
	&__img {
		@include mq($max-width: 767px) {
			margin-bottom: 50px;
			margin-left: auto;
			margin-right: auto;
			display: block;
			max-width: 500px;
			width: 100%;
		}
	}
}

.checkout-form {
	padding: 30px;
	border-radius: 12px;
	background: #fff;
	color: #333;
	@include mq($max-width: 470px) {
		padding: 30px 20px;
	}
	.color-blue {
		color: #58a4fd;
	}
	&__title {
		padding-bottom: 30px;
		font-size: 32px;
		font-weight: 500;
		line-height: normal;
		@include mq($max-width: 767px) {
			font-size: 24px;
		}
	}
	&__section {
		height: 100%;
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0px;
		}
	}
	&__section-title {
		margin-bottom: 15px;
		color: #333;
		font-size: 20px;
		font-weight: 500;
	}
	&__item {
		margin-bottom: 20px;
	}
	&__error {
		color: red;
		font-size: 14px;
	}
	&__input,
	input:not([type='checkbox']),
	select {
		padding: 5px 15px;
		height: 40px;
		width: 100%;
		border-radius: 20px;
		border: 1px solid #c9c9c9;
		font-size: 16px;
		font-weight: normal;
		line-height: normal;
		letter-spacing: 0.8px;
		color: #333;
		background: #fff;
		&:focus {
			border-color: #58a4fd;
		}
	}
	input::placeholder {
		font-size: 16px;
		color: #797979;
	}
	select {
		appearance: none;
		padding-right: 45px;
		background-image: linear-gradient(45deg, transparent 50%, #555 50%), linear-gradient(135deg, #555 50%, transparent 50%);
		background-position: calc(100% - 21px) 50%, calc(100% - 15px) 50%;
		background-size: 6px 6px, 6px 6px, 40px 100%;
		background-repeat: no-repeat;
	}

	.iti {
		width: 100%;
		.iti__flag-container {
			.iti__selected-flag {
				border-radius: 20px 0 0 20px;
				border-right: 1px solid #c9c9c9;
				background-color: #fff;
			}
			input {
				border: 1px solid 1px solid #c9c9c9;
			}
		}
		.iti__arrow {
			border-left: 6px solid transparent;
			border-right: 6px solid transparent;
			border-top: 6px solid #555;
			&.iti__arrow--up {
				border-top: none;
				border-bottom: 6px solid #555;
			}
		}
	}

	&__checkbox {
		input[type='checkbox'] {
			margin-right: 10px;
			appearance: none;
			width: 20px;
			height: 20px;
			border: 1px solid #c9c9c9;
			border-radius: 3px;
			position: relative;
			&:checked {
				background-color: #58a4fd;
				border-color: #58a4fd;
				text-align: center;
				&:before {
					content: '\f00c';
					font-family: 'Font Awesome 5 Free';
					font-weight: 700;
					color: #fff;
					font-size: 14px;
					display: inline-block;
					line-height: 20px;
				}
			}
		}
		label {
			display: flex;
			a {
				display: inline-block;
				padding: 0 5px;
				&:hover {
					color: #58a4fd;
				}
			}
			p {
				line-height: 22px;
			}
		}
	}

	&__terms {
		padding-top: 15px;
		font-size: 14px;
		font-weight: normal;
		line-height: normal;
		text-align: center;
		color: #666;
	}
	.iti--allow-dropdown .iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly])) .iti__selected-country-primary:hover,
	.iti--allow-dropdown
		.iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly]))
		.iti__selected-country:has(+ .iti__dropdown-content:hover)
		.iti__selected-country-primary,
	.iti--allow-dropdown
		.iti__country-container:not(:has(+ input[disabled])):not(:has(+ input[readonly]))
		.iti__selected-country:has(+ .iti__dropdown-content:hover)
		.iti__selected-country-primary {
		border-radius: 20px 0 0 20px;
	}
}

.checkout-summary {
	margin-bottom: 20px;
	padding: 20px;
	border-radius: 16px;
	background: #f7f7f7;

	&__row {
		margin-bottom: 20px;
		display: flex;
		font-size: 16px;
		&--total {
			padding-top: 15px;
			margin-bottom: 0;
			border-top: 2px solid #ededed;
			font-size: 18px;
			font-weight: 600;
			.checkout-summary__value {
				font-size: 24px;
				font-weight: 700;
			}
		}
	}
	&__value {
		font-weight: 500;
	}
	&__label-info {
		flex: 1 0 auto;
		text-align: right;
		font-style: italic;
		font-size: 14px;
	}
}

.checkout-payment {
	display: flex;
	flex-direction: column;
	gap: 20px;
	&__submit-button {
		width: 100%;
	}
	&__terms {
		margin-top: 10px;
		font-size: 12px;
		font-weight: 400;
		line-height: 1.5;
	}
}
