@use 'sass:color';
$s3url: 'https://s3-us-west-2.amazonaws.com/www.bookingninjas.com';
// Colors
$color-primary: #3e63f5;
$color-success: #3acf64;
$color-warning: #ffee53;
$color-danger: #ff5b5b;
$text-color: #333333;
$heading-color: #202020;
$link-color: #3e63f5;
$link-color-hover: color.adjust($link-color, $lightness: -15%);

$color-dark: #333333;
$strokes: #96949a;
$color-primary-light: #eff9ff;

// Typography
$font-size: 16px;
$font-family: 'Montserrat', sans-serif;
$font-family-secondary: 'Nexa', sans-serif;

// Buttons
$button-primary-bg: $color-primary;
$button-secondary-bg: $color-dark;
$button-whatsapp-bg: #07c30a;
$button-success-bg: #3acf64;
$button-danger-bg: #ff5b5b;
$button-warning-bg: #ffee53;
$button-outline-bg: #ffffff;

$button-text-color: #ffffff;
$button-outline-text-color: $color-dark;
$button-outline-border-color: $color-dark;
$button-outline-hover-color: $color-primary;

$button-inverse-bg: #ffffff;
$button-inverse-text-color: $color-dark;

$button-padding-horizontal: 32px;
$button-padding-vertical: 8px;
$button-font-size: 20px;
$button-font-weight: 700;
$button-line-height: 26px;
$button-border-radius: 32px;
$button-transition: 0.3s ease;
$button-disabled-opacity: 0.6;

// Borders
$border-radius: 24px;
$border-color: #96949a;

// Backgrounds and Gradients
$bg-1: #f1f1f1;
$gradient-1: linear-gradient(to bottom, #eef1f7, #ffffff);
$card-bg: #fbfdff;
$card-accent-bg: #333333;
$bg-img: url('#{$s3url}/img/bg-pattern.png');

// Gaps
$gap: 20px;
$gap-list-icon: 8px;

// Breakpoints
$breakpoints: (
	'sm': 576px,
	'md': 768px,
	'lg': 992px,
	'xl': 1200px,
	'xxl': 1400px,
);

// Spacing Scale
$spacing-scale: (
	'0': 0px,
	'1': 4px,
	'2': 8px,
	'3': 12px,
	'4': 16px,
	'5': 20px,
	'6': 24px,
	'7': 28px,
	'8': 32px,
	'9': 36px,
	'10': 40px,
	'12': 48px,
	'14': 56px,
	'16': 64px,
	'20': 80px,
	'24': 96px,
	'28': 112px,
	'32': 128px,
	'36': 144px,
	'40': 160px,
	'44': 176px,
	'48': 192px,
	'52': 208px,
	'56': 224px,
	'60': 240px,
	'64': 256px,
	'72': 288px,
	'80': 320px,
	'96': 384px,
	'auto': auto,
);
